@use "vars";

body.dark {

  background-image: none;
  background-color: vars.$dark !important;
  color: vars.$white !important;

  main {
    aside {
      &.left-navigation {
        border-right: none;
        background: #343a40;
        border-right: 1px solid vars.$gray-900;
      }
    }
  }

  input.form-control,
  select.form-control,
  textarea,
  table,
  table th,
  table td,
  div.card,
  aside,
  nav.card,
  ul.pagination li.page-item a.page-link,
  footer,
  div.modal-content,
  div.list-group,
  a.list-group-item {

    background-color: vars.$dark !important;
    color: vars.$white !important;
  }

  div.card,
  div.modal-content,
  a.list-group-item {
    border: 1px solid vars.$white !important;
  }

  .dropdown-menu {
    border: 1px solid vars.$white !important;
    background-color: vars.$dark !important;
    color: vars.$white !important;
  }

  a, i.bi {
    color: vars.$white !important;
  }

  .dropdown-item:hover {
    background-color: vars.$primary !important;
  }

  ul.pagination li.page-item.active a.page-link {
    color: #212529 !important;
    background-color: #fff !important;
    border-color: transparent;
  }

  footer {
    border-top: 0;
  }

}